body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  font-size: 10px;
}
body {
  background-color: black;
  margin: 0;
  min-height: 100vh;
  box-sizing: border-box;
  font-family: "Do Hyeon", sans-serif;
  background-image: url(/static/media/darkmode_bg.317d79f3.svg);
  background-repeat: no-repeat;
  background-size: cover;
}

h1,
p {
  color: white;
}

/* 네비게이션 바 */
.main-logo {
  font-size: 3.5rem;
}
.main-nav {
  display: flex;
  color: white;
  justify-content: center;
  align-items: center;
  padding: 10px;
  font-size: 2rem;
  font-weight: 700;
  transition: 0.4s ease-out;
}
.flexgrowBlank {
  flex-grow: 0.9;
}

.main-nav a:hover {
  background-color: rgba(110, 175, 136, 0.9);
  padding: 7px;
  transition: 0.4s ease-out;
  border-radius: 100px;
  cursor: pointer;
}
.main-nav a:link {
  color: white;
  text-decoration: none;
}

.main-nav a:visited {
  color: white;
  text-decoration: none;
}
.main-nav .active {
  transform: translateY(-20px);
  transition: 0.4s ease-out;
}
/* 메인 컨텐츠 부분 */
.wrapper {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  font-weight: 600;
  margin-top: 20px;
  margin-bottom: 70px;
}
.main-board,
.main-til,
.main-summary {
  width: 22%;
  position: relative;
  transition: 0.4s ease-out;
}
.board-content,
.til-content,
.summary-content {
  width: 100%;
  border-radius: 20px;
  /* background-color: #222121; */
  background-color: rgba(34, 33, 33, 0.7);
  box-shadow: 8px 8px 22px #545454;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: space-evenly; */
  height: 560px;
  overflow: auto;
}

.main-board:hover,
.main-til:hover,
.main-summary:hover {
  transform: translateY(-20px);
  transition: 0.4s ease-out;
}

.content {
  padding: 20px;
  background-color: white;
  border-radius: 20px;
  width: 75%;
  cursor: pointer;
  margin: 10px;
}
.content:hover {
  box-shadow: 7px 7px 10px grey;
}
.subtitle {
  color: black;
  font-size: 2rem;
  margin: 0;
}
.addContent {
  position: absolute;
  right: 0;
}
.addContent:visited {
  color: white;
}
.addContent:link {
  color: white;
}
/* regist 부분*/

#mainform {
  max-width: 500px;
  margin: 0 auto;
}

#labelform {
  line-height: 2;
  display: block;
  margin-bottom: 8px;
  margin-top: 20px;
  font-size: 17px;
  text-align: left;
  font-weight: 200;
}

.alert {
  color: #bf1650;
  font-size: 16px;
}

.alert::before {
  display: inline;
  content: "⚠ ";
}

.maininput {
  display: block;
  box-sizing: border-box;
  width: 100%;
  border-radius: 4px;
  border: 1px solid white;
  padding: 10px 15px;
  margin-bottom: 8px;
  font-size: 14px;
}

#submit {
  text-transform: uppercase;
  margin-top: 20px;
  padding: 10px;
  font-size: 14px;
  font-weight: 100;
  letter-spacing: 1px;
  background-color: rgba(47, 73, 58, 0.9);
  color: white;
}

#submit:hover {
  background: rgba(125, 194, 152, 0.9);
}

#submit:active {
  transition: 0.3s all;
  transform: translateY(3px);
  border: 1px solid transparent;
  opacity: 0.8;
}

.maininput:disabled {
  opacity: 0.4;
}

/* footer부분 */
.footer {
  font-size: 1.5rem;
  display: flex;
  justify-content: center;
  margin-top: auto;
}

/* 반응형 (모바일) */
@media screen and (max-width: 786px) {
  .wrapper {
    flex-direction: column;
  }
  .main-board,
  .main-til,
  .main-summary {
    width: 70%;
    margin-bottom: 70px;
  }
}
.profile {
  width: 4%;
  border-radius: 100px;
}

/* LightMode일 경우 */
@media (prefers-color-scheme: light) {
  body {
    background-image: url(/static/media/lightmode_bg.cda29fed.svg);
  }
  .board-content,
  .til-content,
  .summary-content {
    background-color: rgba(234, 235, 250, 0.5);
  }

  .main-nav a:hover {
    background-color: rgba(114, 219, 223, 0.9);
  }
}

/* AttendContent */

.wrapperAttend {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-evenly;
  align-items: stretch;
  margin-top: 20px;
  margin-bottom: 70px;
  padding: 40px;
}

.NavAttend {
  order: 0;
  flex: 1 1 auto;
  margin-left: 100px;
  margin: 20px;
  border-radius: 20px;
  background-color: rgba(234, 235, 250, 0.5);
  box-shadow: 8px 8px 22px #545454;
  font-size: 20px;
  line-height: 80px;
}

.MainAttend {
  order: 1;
  flex: 4 1 auto;
  margin-left: 100px;
  margin: 20px;
  border-radius: 20px;
  background-color: rgba(234, 235, 250, 0.5);
  box-shadow: 8px 8px 22px #545454;
}

.AttendTable {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-evenly;
}

.WeekIdx {
  order: 0;
  flex: 1 1 auto;
  font-size: 20px;
  border-radius: 20px;
  margin: 10px;
  text-align: center;
  background-color: white;
  width: 50px;
}

.Undefined {
  order: 1;
  flex: 2 1 auto;
  font-size: 20px;
  border-radius: 20px;
  margin: 10px;
  text-align: center;
}

.present {
  order: 1;
  flex: 2 1;
  font-size: 20px;
  border-radius: 20px;
  margin: 10px;
  text-align: center;
  background-color: #2b6df4;
}

.absent {
  order: 1;
  flex: 2 1;
  font-size: 20px;
  border-radius: 20px;
  margin: 10px;
  text-align: center;
  background-color: black;
  color: white;
}

.modal {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99;
  background-color: rgba(0, 0, 0, 0.6);
}
.modal button {
  outline: none;
  cursor: pointer;
  border: 0;
}
.modal > section {
  width: 90%;
  max-width: 450px;
  margin: 0 auto;
  border-radius: 0.3rem;
  background-color: #fff;
  /* 팝업이 열릴때 스르륵 열리는 효과 */
  animation: modal-show 0.3s;
  overflow: hidden;
}
.modal > section > header {
  position: relative;
  padding: 16px 64px 16px 16px;
  background-color: black;
  font-weight: 700;
}
.modal > section > header button {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 30px;
  font-size: 21px;
  font-weight: 700;
  text-align: center;
  color: #999;
  background-color: transparent;
}
.modal > section > main {
  padding: 16px;
  border-bottom: 1px solid #dee2e6;
  border-top: 1px solid #dee2e6;
  color: black;
}
.modal > section > footer {
  padding: 12px 16px;
  text-align: right;
}
.modal > section > footer button {
  padding: 6px 12px;
  color: #fff;
  background-color: #6c757d;
  border-radius: 5px;
  font-size: 13px;
}
.modal.openModal {
  display: flex;
  align-items: center;
  /* 팝업이 열릴때 스르륵 열리는 효과 */
  animation: modal-bg-show 0.3s;
}
@keyframes modal-show {
  from {
    opacity: 0;
    margin-top: -50px;
  }
  to {
    opacity: 1;
    margin-top: 0;
  }
}
@keyframes modal-bg-show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

